<template>
	<view class="content">
		<!-- Ê©ôËâ≤Ê∏êÂèòËÉåÊôØ -->
		<view class="gradient-bg"></view>
		
		<!-- BannerËΩÆÊí≠Âõæ -->
		<view class="banner-container" v-if="bannerList.length > 0">
			<swiper class="banner-swiper" :autoplay="true" :interval="3000" :duration="500">
				<swiper-item v-for="(banner, index) in bannerList" :key="index">
					<image 
						:src="banner.imageUrl || banner.image" 
						class="banner-image" 
						mode="aspectFill"
						@click="handleBannerClick(banner)"
					></image>
				</swiper-item>
			</swiper>
		</view>
		
		<!-- ÂäüËÉΩÂõæÊ†áÂå∫Âüü -->
		<view class="function-icons">
			<view class="icon-item" @click="showQueuePopup">
				<view class="icon-wrapper">
					<text class="icon">‚è∞</text>
				</view>
				<text class="icon-text">ÊéíÈòü</text>
			</view>
			<view class="icon-item" @click="goToStore">
				<view class="icon-wrapper">
					<text class="icon">üè™</text>
				</view>
				<text class="icon-text">‰∏ªÈ¢ò‰ªãÁªç</text>
			</view>
			<view class="icon-item" @click="showGameTip">
				<view class="icon-wrapper">
					<text class="icon">üéÆ</text>
				</view>
				<text class="icon-text">Ê∏∏Êàè</text>
			</view>
		</view>
		
		<!-- Èó®Á•®Â±ïÁ§∫Âå∫Âüü -->
		<view class="ticket-section">
			<view class="section-title">ÁÉ≠Èó®Èó®Á•®</view>
			<view class="ticket-list">
				<view class="ticket-item" 
					  v-for="ticket in displayTickets" 
					  :key="ticket.id"
					  @click="goToTicketDetail(ticket.id)">
					<!-- Â∞ÅÈù¢Âõæ -->
					<view class="ticket-cover" v-if="ticket.posterUrl">
						<image :src="ticket.posterUrl" 
							   class="cover-image" 
							   mode="aspectFill"></image>
					</view>
					
					<!-- Á•®Âä°‰ø°ÊÅØ -->
					<view class="ticket-info">
						<text class="ticket-name">{{ ticket.name }}</text>
						
						<!-- ‰ª∑Ê†º‰ø°ÊÅØ -->
						<view class="price-info">
							<!-- ÊúâÂéü‰ª∑‰∏î‰∏éÁé∞‰ª∑‰∏çÂêåÊó∂ÊòæÁ§∫Âéü‰ª∑ -->
							<text class="original-price" 
								  v-if="ticket.originalPrice && ticket.originalPrice !== ticket.currentPrice">
								¬•{{ formatPrice(ticket.originalPrice) }}
							</text>
							<!-- Áé∞‰ª∑ -->
							<text class="current-price">
								¬•{{ formatPrice(ticket.currentPrice) }}
							</text>
						</view>
					</view>
					
					<!-- Êä¢Ë¥≠ÊåâÈíÆ -->
					<view class="buy-button">
						<text class="buy-text">Êä¢Ë¥≠</text>
					</view>
				</view>
			</view>
			<view class="show-all" @click="goToTicketList">
				<text class="show-all-text">ÊòæÁ§∫ÂÖ®ÈÉ®</text>
			</view>
		</view>
		
		<!-- Ê∏∏ÊàèÈ°πÁõÆÂå∫Âüü -->
		<view class="game-section">
			<view class="section-title">Ê∏∏ÊàèÈ°πÁõÆ</view>
			<scroll-view class="game-scroll" scroll-x="true" show-scrollbar="false">
				<view class="game-list">
					<view class="game-item" 
						  v-for="theme in themeList" 
						  :key="theme.id">
						<view class="game-cover">
							<image :src="theme.imageUrl || theme.image" 
								   class="game-image" 
								   mode="aspectFill"></image>
						</view>
						<text class="game-name">{{ theme.themeName || theme.name }}</text>
					</view>
				</view>
			</scroll-view>
		</view>
		
		<!-- Ê¥ªÂä®Âå∫Âüü -->
		<view class="activity-section" v-if="activityList.length > 0">
			<view class="section-title">Á≤æÂΩ©Ê¥ªÂä®</view>
			<view class="activity-container">
				<swiper class="activity-swiper" :autoplay="true" :interval="4000" :duration="500">
					<swiper-item v-for="(activity, index) in activityList" :key="index">
						<image 
							:src="activity.imageUrl || activity.image" 
							class="activity-image" 
							mode="aspectFill"
							@click="handleBannerClick(activity)"
						></image>
					</swiper-item>
				</swiper>
			</view>
		</view>
	</view>

	<!-- ÊéíÈòüÊó∂Èó¥ÂºπÂá∫Â±Ç -->
	<uni-popup ref="queuePopup" type="center" :mask-click="true">
		<view class="queue-popup">
			<view class="queue-popup-header">
				<text class="queue-popup-title">ÊéíÈòüÊó∂Èó¥</text>
			</view>
			<view class="queue-popup-content">
				<view v-if="themeList.length === 0" class="no-data">
					<text>ÊöÇÊó†‰∏ªÈ¢òÊï∞ÊçÆ</text>
				</view>
				<view v-else class="queue-list">
					<view v-for="(theme, index) in themeList" :key="index" class="queue-item">
						<view class="theme-info">
							<text class="theme-name">{{ theme.themeName || theme.name || 'Êú™Áü•‰∏ªÈ¢ò' }}</text>
							<text class="queue-duration">{{ formatQueueDuration(theme.queueDuration || 0) }}</text>
						</view>
					</view>
				</view>
			</view>
		</view>
	</uni-popup>

	<tab-bar />
</template>

<script setup>
import { ref } from 'vue'
import { onLoad } from '@dcloudio/uni-app'
import { banner as bannerAPI, theme as themeAPI } from '@/api'
import { setFromSalesId } from '@/common/storage'

const bannerList = ref([])
const themeList = ref([])
const queuePopup = ref(null)

// Ê†ºÂºèÂåñÊéíÈòüÊó∂Èó¥ÊòæÁ§∫
const formatQueueDuration = (minutes) => {
	if (minutes >= 60) {
		const hours = Math.floor(minutes / 60)
		const remainingMinutes = minutes % 60
		if (remainingMinutes === 0) {
			return `${hours}Â∞èÊó∂`
		} else {
			return `${hours}Â∞èÊó∂${remainingMinutes}ÂàÜÈíü`
		}
	} else {
		return `${minutes}ÂàÜÈíü`
	}
}

// Ëé∑ÂèñbannerÂàóË°®
const getBannerList = async () => {
	try {
		const res = await bannerAPI.query()
		if (res && res.records) {
			bannerList.value = res.records
		}
	} catch (error) {
		console.error('Ëé∑ÂèñbannerÂàóË°®Â§±Ë¥•:', error)
	}
}

// Ëé∑Âèñ‰∏ªÈ¢òÂàóË°®
const getThemeList = async () => {
	try {
		const res = await themeAPI.query()
		if (res && res.records) {
			themeList.value = res.records
		}
	} catch (error) {
		console.error('Ëé∑Âèñ‰∏ªÈ¢òÂàóË°®Â§±Ë¥•:', error)
	}
}

// Á´ãÂç≥Ë¥≠‰π∞ÊåâÈíÆÁÇπÂáª‰∫ã‰ª∂
const handleBuyNow = () => {
	const themeId = themeList.value[0].id
	uni.navigateTo({
		url: `/pages/base/pages/store?id=${themeId}`
	})
}

// ÊòæÁ§∫ÊéíÈòüÊó∂Èó¥ÂºπÂá∫Â±Ç
const showQueuePopup = async () => {
	// ÈáçÊñ∞Ëé∑ÂèñÊúÄÊñ∞ÁöÑ‰∏ªÈ¢òÊï∞ÊçÆ
	await getThemeList()
	queuePopup.value.open()
}

// ÂÖ≥Èó≠ÊéíÈòüÊó∂Èó¥ÂºπÂá∫Â±Ç
const closeQueuePopup = () => {
	queuePopup.value.close()
}

// BannerÁÇπÂáª‰∫ã‰ª∂Â§ÑÁêÜ
const handleBannerClick = (banner) => {
	if (!banner.redirectionPath) {
		return
	}
	
	const redirectionPath = banner.redirectionPath
	
	// Â¶ÇÊûúÊòØhttpÂºÄÂ§¥ÔºåÊâìÂºÄwebview
	if (redirectionPath.startsWith('http')) {
		const title = banner.title || banner.name || 'ÁΩëÈ°µÊµèËßà'
		uni.navigateTo({
			url: `/pages/base/pages/webview?url=${encodeURIComponent(redirectionPath)}&title=${encodeURIComponent(title)}`
		})
	}
	// Â¶ÇÊûúÊòØ/pagesÂºÄÂ§¥ÔºåË∑≥ËΩ¨Â∞èÁ®ãÂ∫èÈ°µÈù¢
	else if (redirectionPath.startsWith('/pages')) {
		uni.navigateTo({
			url: redirectionPath
		})
	}
	// ÂÖ∂‰ªñÊÉÖÂÜµÔºåÂ∞ùËØï‰Ωú‰∏∫È°µÈù¢Ë∑ØÂæÑÂ§ÑÁêÜ
	else {
		try {
			uni.navigateTo({
				url: redirectionPath
			})
		} catch (error) {
			console.warn('Êó†ÊïàÁöÑË∑≥ËΩ¨Ë∑ØÂæÑ:', redirectionPath, error)
		}
	}
}

// È°µÈù¢Âä†ËΩΩÊó∂Ëé∑ÂèñÂèÇÊï∞
onLoad((options) => {
	const { scene = ''} = options;
	// Ê£ÄÊü•ÊòØÂê¶ÊúâsalesÂèÇÊï∞
	const sales = scene && decodeURIComponent(scene).split('=')[1]
	if(sales) {
		setFromSalesId(sales)
	}
	
	// Ëé∑ÂèñÊï∞ÊçÆ
	getBannerList()
	getThemeList()
})

</script>

<style>
.content {
	padding: 0;
	position: relative;
	height: calc(100vh - 100rpx);
}

.banner-container {
	width: 100%;
	height: calc(100vh - 100rpx);
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
}

.banner-swiper {
	width: 100%;
	height: 100%;
}

.banner-image {
	width: 100%;
	height: 100%;
	border-radius: 0;
}

/* ËΩÆÊí≠ÊåáÁ§∫ÁÇπÊ†∑Âºè */
.banner-swiper ::v-deep .uni-swiper-dot {
	background-color: rgba(255, 255, 255, 0.5);
}

.banner-swiper ::v-deep .uni-swiper-dot-active {
	background-color: #ffffff;
}

/* ‰∏ªÈ¢òË¥≠‰π∞ÊåâÈíÆÊ†∑Âºè */
.theme-container {
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	z-index: 10;
	text-align: center;
}

/* ÊåâÈíÆÊ†∑ÂºèÂ∑≤ÁßªËá≥ÁªÑ‰ª∂ÂÜÖÈÉ® */

/* Êü•ÁúãÊéíÈòüÊó∂Èó¥ÊåâÈíÆÊ†∑Âºè */
.queue-button-container {
	position: fixed;
	top: calc(50% + 120rpx);
	left: 50%;
	transform: translateX(-50%);
	z-index: 10;
	text-align: center;
}

/* ÊéíÈòüÊó∂Èó¥ÂºπÂá∫Â±ÇÊ†∑Âºè */
.queue-popup {
	width: 600rpx;
	background-color: #ffffff;
	border-radius: 20rpx;
	overflow: hidden;
}

.queue-popup-header {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 30rpx 40rpx;
	background-color: #f8f9fa;
	border-bottom: 1rpx solid #e9ecef;
}

.queue-popup-title {
	font-size: 36rpx;
	font-weight: bold;
	color: #333333;
}

.close-icon {
	font-size: 40rpx;
	color: #666666;
	line-height: 1;
}

.queue-popup-content {
	padding: 40rpx;
	max-height: 800rpx;
	overflow-y: auto;
}

.no-data {
	text-align: center;
	padding: 80rpx 0;
	color: #999999;
	font-size: 28rpx;
}

.queue-list {
	display: flex;
	flex-direction: column;
	gap: 20rpx;
}

.queue-item {
	padding: 30rpx;
	background-color: #f8f9fa;
	border-radius: 12rpx;
	border: 1rpx solid #e9ecef;
}

.theme-info {
	display: flex;
	justify-content: space-between;
	align-items: center;
}

.theme-name {
	font-size: 32rpx;
	color: #333333;
	font-weight: 500;
	flex: 1;
}

.queue-duration {
	font-size: 28rpx;
	color: #007aff;
	font-weight: bold;
}
</style>
